@import url('https://fonts.googleapis.com/css?family=Roboto');
@import url('https://fonts.googleapis.com/css?family=Indie+Flower');
h1, h2, h3, h4, h5, h6 {
	font-weight: lighter;
}
/* p {
	font-family: M
} */
connexionAlert {
		margin-top: 50px;
}
img.kitty {
	display: block;
	margin-left: auto;
	margin-right: auto;
	width: 30%;
}
.navbar-default {
	background: -webkit-linear-gradient(45deg, #0CAA94 0%, #13c9b0 100%);
	background: -o-linear-gradient(45deg, #0CAA94 0%, #13c9b0 100%);
	background: linear-gradient(45deg, #0CAA94 0%, #13c9b0 100%);
}
.navbar-default .navbar-brand  {
	color: white;
	font-size: 22px;
	margin-left:-50px !important;
}

.navbar-default .navbar-nav>li>a {
	color:white;
}

.panel-default>.panel-heading {
	background: -webkit-linear-gradient(45deg, #0CAA94 0%, #13c9b0 100%);
	background: -o-linear-gradient(45deg, #0CAA94 0%, #13c9b0 100%);
	background: linear-gradient(45deg, #0CAA94 0%, #13c9b0 100%);
	color: white;
}

.buttonTX{
	background: -webkit-linear-gradient(45deg, #0CAA94 0%, #13c9b0 100%);
	background: -o-linear-gradient(45deg, #0CAA94 0%, #13c9b0 100%);
	background: linear-gradient(45deg, #0CAA94 0%, #13c9b0 100%);
	color: white;
}

.ownerButton{
	background: white;
	color: black;
	margin-right: -50px;
}

.headerConnexion {
	font-size: 22px;
	display: block;
	margin-left: auto;
	margin-right: auto;
}
.alertHeader {
	background-color: #eceff1;
	display: -ms-flexbox;
	display: flex;
	-ms-flex-pack: center;
	    justify-content: center;
	-ms-flex-align: center;
	    align-items: center;
}


.connexionBox {
	background-color: #eceff1;
	display: -ms-flexbox;
	display: flex;
	-ms-flex-pack: center;
	    justify-content: center;
	-ms-flex-align: center;
	    align-items: center;
	min-height: 100vh;
}



@media all and (max-width:500px) {
	
	h1 {
		font-size: 1.3em;
	}
	.card {
		margin: 0px;
	}
	header {
		margin: 0;
	}
}


/* ----------------------------
	NotFound
---------------------------- */

.notFound {
	display: -ms-flexbox;
	display: flex;
	-ms-flex-pack: center;
	    justify-content: center;
	-ms-flex-align: center;
	    align-items: center;
	min-height: 100vh;
	margin: 0;
	background: #eceff1;
}



